import React, { useState, useRef, useLayoutEffect } from "react";
import "../Styles/FavoritePage.css";
import { wait, introPageTransition } from "../dependencies";
import { S } from "selector_dom";
import { userData, data } from "../Data";
import ItemCard from "../Foods/ItemCard";
import MealCard from "../Meals/MealCard";
import lottie from "lottie-web";
import lottieNoResults from "../assets/lottieNoResults.json";
import lottieEmpty from "../assets/lottieEmpty.json";

export default function FavoritePage(props) {
  const renderFoodData = (alphabet) => {
    let favoritedata = [...userData.favorite.food.map((e) => (e = Object.assign({}, data.filter((el) => el.id === e)[0])))];
    favoritedata = alphabet
      ? favoritedata.sort((a, b) => {
          const nameA = a.name.toUpperCase();
          const nameB = b.name.toUpperCase();
          if (nameA < nameB) return -1;
          if (nameA > nameB) return 1;
          return 0;
        })
      : favoritedata;
    return favoritedata.map((e) => (
      <ItemCard
        key={e.id}
        name={e.name}
        kcal={e.serving ? Math.round((e.kcal / 100) * e.serving[0][1]) : e.kcal}
        serving={e.serving ? e.serving[0][0] : "كل (100) غرام"}
        id={e.id}
        brand={e.brand}
        description={e.description}
      />
    ));
  };
  const renderMealData = (alphabet) => {
    let ComponentArray = [];
    let dataCollection = [...userData.favorite.meals];
    dataCollection = alphabet
      ? dataCollection.sort((a, b) => {
          const nameA = a.mealName.toUpperCase();
          const nameB = b.mealName.toUpperCase();
          if (nameA < nameB) return -1;
          if (nameA > nameB) return 1;
          return 0;
        })
      : userData.favorite.meals;

    for (let i in dataCollection) {
      const mealdata = dataCollection[i].content.map(
        (e) => (e = Object.assign({}, data.filter((el) => el.id === e[0])[0], { value: e[1], input: e[2] }))
      );
      const kcalSum = mealdata.map((e) => (e.kcal * (e.value * e.input)) / 100).reduce((a, b) => a + b);
      ComponentArray.push(
        <MealCard
          key={Math.random() * 1000}
          name={dataCollection[i].mealName}
          kcal={kcalSum}
          ingredients={dataCollection[i].content.length}
        />
      );
    }
    return ComponentArray;
  };

  const [foodRender, setFoodRender] = useState(renderFoodData());
  const [mealRender, setMealRender] = useState(renderMealData());

  const foodHeight = useRef("auto");
  const mealHeight = useRef("auto");

  useLayoutEffect(() => {
    window.scrollTo({ top: 0 });
    props.doAnimation ? introPageTransition("favoritePage") : introPageFadeTransition();
    renderLottie();
    // set static variables only if the favorite is not empty
    if (renderFoodData().length !== 0) foodHeight.current = S("#favoritePageBodyFood").getStyle("height");
    if (renderMealData().length !== 0) mealHeight.current = S("#favoritePageBodymeal").getStyle("height");

    const container = S("#favoritePageBodyContainer");
    const containerWidth = container.getStyle("width", true);
    // adjust scroll position on resize
    S(window).on("resize", onResizeHandle);

    // Switch to meal tab
    if (props.page === 2 || window.location.hash.includes("#Meals")) {
      // set the height of the container to remove extra space in meal tab.
      container.css({ height: mealHeight.current });
      // play empty meal animation.
      lottie.play("lottieEmptyMeal");
      // move the white bar to left
      S("#paddle").css({ right: "50%" });
      // scroll to meal tab
      container.scrollTo({ byX: -containerWidth });
      // show or hide the search and sort icons if the meal tab is empty or not.
      if (renderMealData().length === 0) {
        S("#favoritePageSort").css({ display: "none" });
        S("#favoriteSearchSvg").css({ display: "none" });
      } else {
        S("#favoritePageSort").css({ display: "block" });
        S("#favoriteSearchSvg").css({ display: "block" });
      }

      if (props.page === 2) window.history.replaceState("", "", "/favorites#Meals");
    } else {
      // set the height of the container to remove extra space in food tab.
      container.css({ height: foodHeight.current });
      // show or hide the search and sort icons if the food tab is empty or not.
      if (renderFoodData().length === 0) {
        S("#favoritePageSort").css({ display: "none" });
        S("#favoriteSearchSvg").css({ display: "none" });
      } else {
        S("#favoritePageSort").css({ display: "block" });
        S("#favoriteSearchSvg").css({ display: "block" });
      }
    }
    return () => {
      S(window).off("resize", onResizeHandle);
    };
  }, [props.doAnimation, props.page]);

  const onResizeHandle = () => {
    const container = document.getElementById("favoritePageBodyContainer");
    const width = S("#favoritePageBodyContainer").getStyle("width", true);
    if (window.location.hash.includes("Meals")) {
      container.scrollTo({ left: -width });
    } else {
      container.scrollTo({ left: 0 });
    }
  };

  const renderLottie = async () => {
    // animation on empty or no search results.
    lottie.loadAnimation({
      container: document.getElementById("lottieEmptyFood"),
      renderer: "svg",
      loop: false,
      autoplay: true,
      animationData: lottieEmpty,
    });

    lottie.loadAnimation({
      container: document.getElementById("lottieEmptyMeal"),
      renderer: "svg",
      loop: false,
      autoplay: false,
      animationData: lottieEmpty,
      name: "lottieEmptyMeal",
    });

    lottie.loadAnimation({
      container: document.getElementById("lottieNoResultsMeal"),
      renderer: "svg",
      loop: true,
      autoplay: true,
      animationData: lottieNoResults,
    });

    lottie.loadAnimation({
      container: document.getElementById("lottieNoResultsFood"),
      renderer: "svg",
      loop: true,
      autoplay: true,
      animationData: lottieNoResults,
    });
  };

  const switchTabs = async (e) => {
    const container = S("#favoritePageBodyContainer");
    const containerWidth = container.getStyle("width", true);

    // switch to food tab
    if (window.location.hash.includes("#Meals") && e.target.id === "favoritePageFoodsTab") {
      // set container height to auto if the food tab is empty, or set it to a value if it isnt empty to remove extra space.
      if (foodRender.length === 0 || renderFoodData().length === 0) container.css({ height: "auto" });
      else container.css({ height: foodHeight.current });
      // move the white bar under food tab title.
      S("#paddle").css({ right: "0%" });
      // scroll to the top of the tab.
      window.scrollTo({ top: 0 });
      // scroll to food tab.
      container.scrollTo({ byX: containerWidth, behavior: "smooth", pps: 40 });
      // hide or show sort and search buttons if the tab is empty or not.
      if (renderFoodData().length === 0) {
        S("#favoritePageSort").css({ display: "none" });
        S("#favoriteSearchSvg").css({ display: "none" });
      } else {
        S("#favoritePageSort").css({ display: "block" });
        S("#favoriteSearchSvg").css({ display: "block" });
      }
      // manage path and hash
      window.location.hash.includes("Meals") && window.location.hash.length === 6
        ? window.history.replaceState("", "", "/favorites")
        : window.history.replaceState("", "", "/favorites#" + window.location.hash.split("#")[2]);

      // switch to meal tab
    } else if (!window.location.hash.includes("#Meals") && e.target.id === "favoritePageMealsTab") {
      // set container height to auto if the meal tab is empty, or set it to a value if it isnt empty to remove extra space.
      if (mealRender.length === 0 || renderMealData().length === 0) container.css({ height: "auto" });
      else container.css({ height: mealHeight.current });
      // play empty meal animation.
      lottie.play("lottieEmptyMeal");
      // move the white bar under meal tab title.
      S("#paddle").css({ right: "50%" });
      // scroll to the top of the tab.
      window.scrollTo({ top: 0 });
      // scroll to meal tab.
      container.scrollTo({ byX: -containerWidth, behavior: "smooth", pps: 40 });
      // hide or show sort and search buttons if the tab is empty or not.
      if (renderMealData().length === 0) {
        S("#favoritePageSort").css({ display: "none" });
        S("#favoriteSearchSvg").css({ display: "none" });
      } else {
        S("#favoritePageSort").css({ display: "block" });
        S("#favoriteSearchSvg").css({ display: "block" });
      }
      // manage path and hash
      window.location.hash.length === 0
        ? window.history.replaceState("", "", "/favorites#Meals")
        : window.history.replaceState("", "", "/favorites#Meals#" + window.location.hash.split("#")[1]);
    }
  };

  const introPageFadeTransition = () => {
    if (window.location.pathname === "/favorites" && window.location.hash.length === 0)
      S("#favoritePageBack").css({ display: "none" });

    S("#favoritePage").keyframes([{ opacity: "0" }, { opacity: "1" }], { duration: 150, fill: "forwards" });
  };

  const backButtonHandle = () => window.history.back();

  const sortHandle = () => {
    const isScale1 = S("#favoritePagePopUp").getStyle("transform").includes("1");
    S(".mealPopUp").css({ transform: "scale(0)" });
    if (isScale1) {
      S("#favoritePagePopUp").css({ transform: "scale(0)" });
    } else {
      S("#favoritePagePopUp").css({ transform: "scale(1)" });
      document.addEventListener(
        "click",
        (e) => {
          if (e.target.className !== "favoritePagePopUpItems") S("#favoritePagePopUp").css({ transform: "scale(0)" });
        },
        { once: true }
      );
    }
  };

  const toggleSearch = async () => {
    const el = S("#favoriteSearchContainer");
    const header = S("#favoritePageHeader");
    if (el.getStyle("height", true) > 0) {
      S("#favoriteSearchBar").blur();
      searchIconAnimation("close");
      el.animate({}, { height: "0px", padding: "0px" });

      setFoodRender(renderFoodData());
      setMealRender(renderMealData());

      document.getElementById("favoriteSearchBar").value = "";
      await wait(300);
      header.css({ boxShadow: "0px 0px 7px 0px rgba(0, 0, 0, 0.3)" });
    } else {
      searchIconAnimation("open");
      S("#favoriteSearchBar").focus();
      el.animate({}, { height: S("#favoriteSearchBar").getStyle("height"), padding: "15px 0px" });
      header.css({ boxShadow: "unset" });
    }
  };

  const searchIconAnimation = (state) => {
    const circle = S("#favoriteSearchSvg  circle");
    const arm1 = S("#favoriteSearchSvg path:nth-child(2)");
    const arm2 = S("#favoriteSearchSvg path:nth-child(3)");
    if (state === "open") {
      arm2.keyframes([{ transform: "translate(-30%, -30%)" }], { duration: 150, fill: "forwards" });
      arm1.keyframes([{ transform: "none" }, { transform: "translate(-30%, -30%)" }], {
        duration: 150,
        fill: "forwards",
      });
      circle.keyframes([{ transform: "translate(10%, 10%) scale(1.2)", transformOrigin: "center" }], {
        duration: 150,
        fill: "forwards",
      });
      arm1.keyframes(
        [{ transformOrigin: "77% 77%" }, { transform: "translate(-30%, -30%) rotate(-90deg)", transformOrigin: "77% 77%" }],
        {
          delay: 200,
          duration: 150,
          fill: "forwards",
        }
      );
    } else {
      arm1.keyframes(
        [{ transformOrigin: "77% 77%" }, { transform: "translate(-30%, -30%) rotate(0deg)", transformOrigin: "77% 77%" }],
        {
          duration: 150,
          fill: "forwards",
        }
      );
      circle.keyframes([{ transformOrigin: "center" }, { transform: "translate(0%, 0%) scale(1)" }], {
        delay: 200,
        duration: 150,
        fill: "forwards",
      });
      arm1.keyframes([{ transform: "translate(0%, 0%)" }], {
        delay: 200,
        duration: 150,
        fill: "forwards",
      });
      arm2.keyframes([{ transform: "translate(0%, 0%)" }], {
        delay: 200,
        duration: 150,
        fill: "forwards",
      });
    }
  };

  const searchOnChangeHandle = (sort) => {
    window.scrollTo({ top: 0 });
    const container = S("#favoritePageBodyContainer");
    const searchInput = document.getElementById("favoriteSearchBar").value;
    const foodSearchData = renderFoodData(sort).filter(
      (e) => e.props.name.toLowerCase().indexOf(searchInput.toLowerCase()) !== -1
    );
    const mealSearchData = renderMealData(sort).filter(
      (e) => e.props.name.toLowerCase().indexOf(searchInput.toLowerCase()) !== -1
    );
    setFoodRender(foodSearchData);
    setMealRender(mealSearchData);
    if (foodRender.length === 0 && renderFoodData().length !== 0) container.css({ height: "auto" });
    if (mealRender.length === 0 && renderMealData().length !== 0) container.css({ height: "auto" });
  };

  return (
    <div id="favoritePage">
      <header id="favoritePageHeader">
        <div id="favoritePageHeaderContainer">
          <p id="favoritePageTitle">المفضلة</p>
          <svg id="favoritePageBack" onClick={backButtonHandle} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
            <path d="M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z" />
          </svg>
        </div>
        <div id="favoritePageTabsContainer">
          <p id="favoritePageFoodsTab" className="favoritePageTabs" onClick={switchTabs}>
            الطعام
          </p>
          <p
            id="favoritePageMealsTab"
            className="favoritePageTabs"
            onClick={switchTabs}
            style={{
              opacity:
                window.location.pathname.includes("CreateNewMeal") || window.location.pathname.includes("EditMeal") ? "0" : "1",
              pointerEvents:
                window.location.pathname.includes("CreateNewMeal") || window.location.pathname.includes("EditMeal")
                  ? "none"
                  : "initial",
            }}
          >
            الوجبات
          </p>
          <div id="paddle"></div>
        </div>
        <svg id="favoritePageSort" onClick={sortHandle} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
          <path d="M3 18h6v-2H3v2zM3 6v2h18V6H3zm0 7h12v-2H3v2z" />
        </svg>
        <svg id="favoriteSearchSvg" onClick={toggleSearch} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
          <circle cx="9.5" cy="9.5" r="6.5" stroke="white" strokeWidth="2" />
          <path d="M15 15L22 22" stroke="white" strokeWidth="2" strokeLinecap="round" />
          <path d="M15 15L22 22" stroke="white" strokeWidth="2" strokeLinecap="round" />
        </svg>
        <div id="favoritePagePopUp">
          <p
            className="favoritePagePopUpItems"
            id="favoritePagePopUpAB"
            onClick={() => {
              setFoodRender(renderFoodData(true));
              setMealRender(renderMealData(true));
              searchOnChangeHandle(true);
              S("#favoritePagePopUp").css({ transform: "scale(0)" });
            }}
          >
            ترتيب حسب الأبجدية
          </p>
          <p
            className="favoritePagePopUpItems"
            id="favoritePagePopUpNew"
            onClick={() => {
              setFoodRender(renderFoodData(false));
              setMealRender(renderMealData(false));
              searchOnChangeHandle(false);
              S("#favoritePagePopUp").css({ transform: "scale(0)" });
            }}
          >
            ترتيب حسب الأحدث
          </p>
        </div>
      </header>
      <div id="favoriteSearchContainer">
        <input id="favoriteSearchBar" placeholder="بحث" autoComplete="off" type="search" onChange={searchOnChangeHandle} />
      </div>
      <div id="favoritePageBodyContainer">
        <div id="favoritePageBodyFood">
          <div id="lottieEmptyFood" style={{ display: renderFoodData().length === 0 ? "block" : "none" }}>
            <p className="favoritePageIsEmpty">لا يوجد طعام في المفضلة</p>
          </div>
          <div
            id="lottieNoResultsFood"
            style={{ display: foodRender.length === 0 && renderFoodData().length !== 0 ? "block" : "none" }}
          >
            <p className="favoritePageIsEmpty">لا يوجد نتائج</p>
          </div>

          {foodRender}
        </div>
        <div id="favoritePageBodymeal">
          <div id="lottieEmptyMeal" style={{ display: renderMealData().length === 0 ? "block" : "none" }}>
            <p className="favoritePageIsEmpty">لا يوجد وجبات في المفضلة</p>
          </div>
          <div
            id="lottieNoResultsMeal"
            style={{ display: mealRender.length === 0 && renderMealData().length !== 0 ? "block" : "none" }}
          >
            <p className="favoritePageIsEmpty">لا يوجد نتائج</p>
          </div>

          {mealRender}
        </div>
      </div>
    </div>
  );
}
